import { FormsFormPickerBaseElement } from "./form-picker-base.element.js";
import {
  customElement,
  html,
  property,
} from "@umbraco-cms/backoffice/external/lit";
import type { FormsInputFormElement } from "@umbraco-forms/components";
import { splitStringToArray } from "@umbraco-cms/backoffice/utils";

const elementName = "forms-form-picker-single-property-editor";

@customElement(elementName)
export class FormsFormPickerSinglePropertyPickerElement extends FormsFormPickerBaseElement {
  @property({ type: String })
  public value: string = "";

  constructor() {
    super();
    this.allowMultiple = false;
  }

  #onPickedFormsChange(event: Event) {
    const target = event.target as FormsInputFormElement;
    this.value = target.selection.length > 0 ? target.selection[0] : "";
    this.dispatchEvent(new CustomEvent("property-value-change"));
  }

  render() {
    return html`<forms-input-form
      .selection=${splitStringToArray(this.value)}
      .multiple=${this.allowMultiple}
      .allowedFolders=${this.allowedFolders}
      .allowedForms=${this.allowedForms}
      @change=${this.#onPickedFormsChange}
      slot="editor"
    ></forms-input-form>`;
  }
}

export default FormsFormPickerSinglePropertyPickerElement;

declare global {
  interface HTMLElementTagNameMap {
    [elementName]: FormsFormPickerSinglePropertyPickerElement;
  }
}
